.card-body {
    align-items: center;
    padding: 8px;
    width: 100%;
    background-color: #000000;
}
.card-image {
    max-height: 225px;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    box-shadow: 2px 2px 5px rgb(0, 0, 0);
}
.card-image:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 10px 10px 15px rgb(128, 128, 128);

}
.footer {
    width:100%;
    height:17%;
    position:absolute;
    left:0;
    background-color:black;
    color: rgba(255, 255, 255, 0.158);
    text-align: center;
    vertical-align: middle;
    padding: 40px;
}
.header{
    margin: auto;
    height: 15%;
    width: 70%;
    background-color: black;
    color: rgb(251, 255, 15);
    text-align: center;
    vertical-align: middle;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif
}
.score-box{
    margin: auto;
    height: 15%;
    width: 20%;
    background-color: black;
    color: rgb(251, 255, 15);
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

body {
    background-color: black;
    padding-top: 150px;
}

.card-holder {
    background-color: whitesmoke;
    margin: auto;
    width: 90%;
    align-items: center;
    border: 1px solid #efefef;
    padding: 20px;
}
.header-row {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    background-color: black;
    padding-top: 50px;

}
.instructions {
    background-color: yellow;
    color: black;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 1.5em;
    padding-left: 15%;
    text-align: center;
}
